import { TranslateService } from '@ngx-translate/core';
import { GlobalDataService } from './../../../shared/services/globalData/global-data.service';
import { AlertService } from './../../../shared/services/alert/alert.service';
import { AchievementService } from './../services/achievement.service';
import { Achievement, AchievementState, ChallengeType, ChallengeTypeEnum, UserChallengeState } from './../../../shared/models/achievement.model';
import { Component, OnInit } from '@angular/core';
import { ActivatedRoute, Router } from '@angular/router';
import { Toast } from '@capacitor/toast';
import { environment } from 'src/environments/environment';

@Component({
  selector: 'app-achievement-detail',
  templateUrl: './achievement-detail.page.html',
  styleUrls: ['./achievement-detail.page.scss'],
})
export class AchievementDetailPage implements OnInit {

  item: Achievement;
  id: number;
  loadingContent: boolean;
  loading: boolean;

  AchievementState = AchievementState;
  ChallengeType = ChallengeTypeEnum;

  scanning: boolean;
  scanned: boolean;
  dataScanned: string;
  dataScannedText: string;
  startScanText: string;

  environment = environment;
  constructor(
    private router: Router,
    private route: ActivatedRoute,
    private achievementService: AchievementService,
    private alertService: AlertService,
    private globalData: GlobalDataService,
    private translate: TranslateService,
  ) { }

  ngOnInit() {
    this.startScanText = this.translate.instant('PAGES.CHALLENGES.DETAIL.QR_INITIAL');

    this.route.paramMap.subscribe((params) => {
      this.id = parseInt(params.get('id'));
      this.getChallenge();
    });
  }

  async achievementAction() {
    try {
      if(this.item.user_challenge.user_challenge_state.code) {
        this.loading = true;
        if (this.item.user_challenge.user_challenge_state.code === AchievementState.STARTED) {
          const buttons = [
            { text: this.translate.instant('ALERTS.CANCEL'), role: 'cancel', },
            { text: this.translate.instant('ALERTS.ACCEPT'), role: 'accept' }
          ];
          let result: any = await this.alertService.showGameAlert(this.translate.instant('PAGES.CHALLENGES.DETAIL.END_CHALLENGE_TITLE'), this.translate.instant('PAGES.CHALLENGES.DETAIL.END_CHALLENGE_TEXT'), buttons);
          if (result && result.data && result.role === 'accept') this.item = (await this.achievementService.processChallenge(this.item.id, { scannedData: this.dataScanned }).toPromise()).achievement;
        } else {
          let data = await this.achievementService.processChallenge(this.item.id, { scannedData: this.dataScanned }).toPromise();
          console.log('Achievement ', data);
          this.item = data.achievement;
          if (this.item.user_challenge.user_challenge_state.code === AchievementState.FINISHED) {
            this.globalData.player.magentoPoints = data.magentoPoints;
            this.globalData.player.pointsFreeze = data.magentoPoints;
            this.alertService.showGameAlert(this.translate.instant('PAGES.GAME.MESSAGES.YOU_WIN')+data.reward+'m!', this.translate.instant('PAGES.GAME.MESSAGES.CONGRATS')+this.globalData.player.magentoPoints+'m '+this.translate.instant('PAGES.GAME.MESSAGES.STACK'), [this.translate.instant('ALERTS.ACCEPT')])
          }
        }
        this.loading = false;
      }
    } catch (error) {
      console.log(error);
      this.loading = false;
    }
  }

  async getChallenge() {
    try {
      this.loadingContent = true;
      this.item = await this.achievementService.getChallenge(this.id).toPromise();
      this.showContent();
      console.log('Challenge ', this.item);
      this.loadingContent = false;
    } catch (error) {
      console.log(error);
      this.loadingContent = false;
    }
  }

  async showContent(data?) {
    this.scanning = false;
    if (data) {
      this.dataScanned = data;
      this.dataScannedText = this.translate.instant('PAGES.CHALLENGES.DETAIL.QR_OK');
      this.scanned = true;
    } else {
      this.dataScanned = undefined;
      this.dataScannedText = undefined;
      this.scanned = false;
    }
    console.log('Data scanned',data, this.scanning, this.dataScanned, this.dataScannedText)
  }

  hideContent() {
    this.scanning = true;
  }

  checkActionUser() {
    let disabled = true;
    switch (this.item?.user_challenge?.user_challenge_state?.code) {
      case AchievementState.STARTED:
        if (this.item?.challenge_type?.code === this.ChallengeType.EVENT) {
          if(this.dataScanned) disabled = false;
        }else disabled = false;
        break;
      case AchievementState.NONE:
        disabled = false;
        break;
      case AchievementState.PENDING:
        disabled = true;
        break;
      case AchievementState.ACCEPTED:
        disabled = false;
        break;
      case AchievementState.FINISHED:
        disabled = true;
        break;
      case AchievementState.REJECTED:
        disabled = true;
        break;
    }
    return disabled;
  }

}
